<template>
  <div class="done">
    <h2 class="text-center mb-4 text-light">Выполнено</h2>
    <div class="done__items" >
      <TransitionGroup class="list-group" name="done" tag="ul">
        <done-item :key="item.id" v-for="item in done" :item="item" />
      </TransitionGroup>
    </div>
    <h2 v-if="done.length === 0" class="text-center mb-4 text-light">Нету выполненых задач</h2>
    <button
      v-if="done.length !== 0"
      class="btn btn-danger fs-4 d-flex m-auto"
      @click="$emit('clearDone', done)"
    >
      Очистить
    </button>
   
  </div>
</template>
<script>
import DoneItem from "@/componets/DoneItem.vue";
export default {
  components: {
    DoneItem,
  },
  props: {
    done: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {};
  },
};
</script>
