<template>
  <section class="pt-5 sec">
    <div class="container">
      <h1 class="text-center mb-4 text-light">Задачник</h1>
      <div class="row">
        <list
          class="col-12 col-md-6"
          @additem="addItem"
          @removeitem="removeItem"
          :items="items"
        />
        <done class="col-12 col-md-6" :done="done" @clearDone="clearDone" />
      </div>
    </div>
  </section>
  <video class="video-bg" autoplay loop>
    <source src="source/bg.mp4" type="video/mp4" />
  </video>
</template>
<script>
import List from "@/componets/List.vue";
import Done from "@/componets/Done.vue";
export default {
  components: {
    List,
    Done,
  },
  data() {
    return {
      items: [],
      done: [],
    };
  },
  methods: {
    addItem(item) {
      this.items.push(item);
      localStorage.items = JSON.stringify(this.items);
    },
    clearDone() {
      this.done = [];
      localStorage.removeItem("done");
    },
    removeItem(i) {
      this.done.push(i);
      localStorage.done = JSON.stringify(this.done);
      this.items = this.items.filter((item) => item.id !== i.id);
      localStorage.items = JSON.stringify(this.items);
    },
  },
  mounted() {
    if (localStorage.items) {
      this.items = JSON.parse(localStorage.getItem("items"));
    }
    if (localStorage.done) {
      this.done = JSON.parse(localStorage.getItem("done"));
    }
  },
};
</script>
<style lang="scss">
@import "@/scss/mixins";
 .video-bg {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
}

body {
  position: relative;
  height: 100%;
  min-width: 320px;

  &:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgb(0, 0, 0, 0.7);
    z-index: 2;
  }
}
.container {
  position: relative;
  z-index: 10;
}
.sec{
  height: 100vh;
  overflow: auto;
}
</style>
