<template>
  <div class="list">
    <controls @additem="$emit('additem', $event)" />
    <TransitionGroup class="list-group" name="list" tag="ul">
      <list-item
        v-for="item of items"
        @click.once="$emit('removeitem', item)"
        :key="item.id"
        :item="item"
      />
    </TransitionGroup>
  </div>
</template>
<script>
import Controls from "@/componets/Controls.vue";
import ListItem from "@/componets/ListItem.vue";
export default {
  components: {
    Controls,
    ListItem,
  },
  props: {
    items: {
      type: Array,
      required: true,
    },
  },

  data() {
    return {};
  },
};
</script>
