<template>
  <div class="row mb-5">
    <div class="col-8">
      <input
        class="form-control fs-md-5"
        type="text"
        placeholder="Имя задачи"
        @input="this.title = $event.target.value"
        :value="title"
        @keypress="$event.key === 'Enter' ? this.addItem() : null"
      />
    </div>
    <div class="col-4">
      <button class="btn btn-success w-100" @click="addItem">Добавить</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      title: "",
    };
  },
  methods: {
    addItem() {
      const item = {
        id: Date.now(),
        title: this.title,
      };
      if (this.title) {
        this.$emit("additem", item);
      }
      this.title = "";
    },
  },
};
</script>

<style lang="scss">
@import "@/scss/mixins";
.btn {
  font-size: 20px;
  @include lap {
    font-size: 16px;
  }
}
.form-control {
  font-size: 20px;
  @include lap {
    font-size: 16px;
  }
}
</style>
